.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 403px;
  padding: 30px 14px 0;
  margin-bottom: 50px;
  margin-right: 48px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__photo {
    width: 160px;
    height: 134px;
    margin: 0 auto;
    margin-bottom: 45px;
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }

  &__code {
    margin-top: 5px;
    margin-bottom: 15px;
  }

  &__stars {
    display: flex;
  }

  &__stars div {
    width: 15px;
    height: 15px;
    margin-right: 4px;
    background-image: url("/images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }

  &__price p:nth-child(1) {
    margin-top: 25px;
  }

  &__price p:nth-child(2) {
    margin-top: 20px;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
  }

  &__button {
    display: block;
    text-align: center;
    width: 170px;
    height: 40px;
    box-sizing: border-box;
    border-radius: 5px;
    border-style: none;
    text-transform: uppercase;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    color: #fff;
    background-color: #00acdc;
    border: 1px solid #00acdc;
  }

  &__link {
    text-decoration: none;
    color: #fff;
  }

}

.card__button:hover {
  border-radius: 5px;
  background-color: #fff;
}

.card__button:hover > a {
  color: #00acdc;
}

.main .card:nth-child(4n+4) {
  margin-right: 0;
}

.stars--4 div:nth-child(-n+4) {
  background-image: url("/images/star-active.svg");
}
